import React from 'react';
import './index.css';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import HomeRoundedIcon from '@material-ui/icons/HomeRounded';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
}));

export default function ButtonAppBar() {

  const classes = useStyles();

  const [state, setState] = React.useState({
    top : false,
    link : '11111',
  });

  const toggleDrawer = (side, open) => event => {
    setState({ ...state, [side]: open });
  };

  const fullList = side => (
    <div
      className={classes.fullList}
      role="presentation"
      onClick={toggleDrawer(side, false)}
    >
      <List>
        <ListItem button key='首頁'>
          <ListItemIcon >{<HomeRoundedIcon />}</ListItemIcon>
          <ListItemText primary='首頁' />
        </ListItem>
        <ListItem button key='個人資料'>
          <ListItemIcon >{<AccountCircleIcon />}</ListItemIcon>
          <ListItemText primary='個人資料' />
        </ListItem>
      </List>
    </div>
  );

  return (
    <div className={classes.root}>
      <Drawer anchor="top" open={state.top} onClose={toggleDrawer('top', false)}>
          {fullList('top')}
      </Drawer>
      <AppBar position="static">
        <Toolbar>
            <IconButton onClick={toggleDrawer('top', true)} edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
                <MenuIcon />
            </IconButton>
            <Typography variant="h6" className={classes.title}>
              歡迎來到Beyond Toy! {state.link}
            </Typography>
            <Button color="inherit">Login</Button>
        </Toolbar>
      </AppBar>
    </div>
  );
}